import GitHubIcon from "@mui/icons-material/GitHub";
import { GoogleAuthProvider, getAuth, signInWithPopup, signOut } from "firebase/auth";
import { useDispatch, useSelector } from "react-redux";
import { ToastContainer, toast } from "react-toastify";
import { addUser } from "../../redux/bazaarSlice";
import { useNavigate } from "react-router-dom";
import { removeUser } from "../../redux/bazaarSlice";
import { useEffect } from "react";

export const Login = () => {
	const productData = useSelector((state) => state.bazar.productData);
	const UserInfo = useSelector((state) => state.bazar.userInfo);

	const auth = getAuth();
	console.log(auth);
	const Provider = new GoogleAuthProvider();
	const dispatch = useDispatch();
	const navigate = useNavigate();

	useEffect(() => {
		console.log(auth?.currentUser, 1);
		if (auth?.currentUser) {
			console.log(auth?.currentUser, 2);
			navigate("/lk");
		}
	}, [auth]);

	const handleGoogleLogin = (e) => {
		e.preventDefault();
		signInWithPopup(auth, Provider)
			.then((result) => {
				const user = result.user;
				dispatch(
					addUser({
						id: user.uid,
						name: user.displayName,
						email: user.email,
						image: user.photoURL,
					}),
				);
				setTimeout(() => {
					navigate("/");
				}, 1500);
			})
			.catch((error) => {
				console.log(error);
			});
	};

	const handleGoogleSignOut = (e) => {
		e.preventDefault();
		signOut(auth)
			.then(() => {
				toast.success("Log Out Successfully");
				dispatch(removeUser());
			})
			.catch((error) => {
				console.log(error);
			});
	};

	return (
		<div className='w-full flex flex-col items-center justify-center gap-10 py-20'>
			<div className='w-fit flex items-center justify-center gap-10'>
				<div
					onClick={handleGoogleLogin}
					className=' flex py-3 px-5 border-[1px] border-black cursor-pointer'
				>
					<img
						className='w-14'
						src={
							"data:image/png;base64,"
						}
						alt=''
					/>
					<span className='text-xl text-gray-900 '>Sign In with Google</span>
				</div>
				<button
					onClick={handleGoogleSignOut}
					className='bg-black text-white text-base py-3 px-8 tracking-wide 
        rounded-md hover:bg-gray-800 duration-300'
				>
					Sign Out
				</button>
			</div>
			<div className='w-fit flex items-center justify-center gap-10'>
				<div className=' flex py-3 px-7 border-[1px] border-black cursor-pointer gap-7'>
					<GitHubIcon />
					<span className='text-xl text-gray-900 '>Sign In with Github</span>
				</div>
				<button
					className='bg-black text-white text-base py-3 px-8 tracking-wide 
        rounded-md hover:bg-gray-800 duration-300'
				>
					Sign Out
				</button>
			</div>
			<ToastContainer
				position='top-left'
				autoClose={2000}
				hideProgressBar={false}
				newestOnTop={false}
				closeOnClick
				rtl={false}
				pauseOnFocusLoss
				draggable
				pauseOnHover
				theme='dark'
			/>
		</div>
	);
};
